.feedback-form {
  max-width: 480px;
  background-color: #db7093;
  border: 2px solid rgba(0, 0, 0, .12);
  border-radius: 8px;
  flex-direction: column;
  padding: 12px;
  display: flex;
}

.feedback-form label {
  flex-direction: column;
  margin-bottom: 16px;
  display: flex;
}

.feedback-form button {
  font: inherit;
  cursor: pointer;
  background-color: #ffa07a;
  border-radius: 8px;
  padding: 8px 12px;
}

input, textarea {
  font: inherit;
  background-color: #bdb76b;
  border-radius: 8px;
  padding: 8px;
}

textarea {
  resize: none;
}

